.randomHeader {
  display: flex;
  justify-content: space-between;

  .title {
    margin: 0;
    line-height: 40px;
  }

  .action {
    float: right;
  }


  .settingIcon {
    font-size: 20px;
  }
}

.randomBody {

  .list {
    padding-left: 7px;
    margin-top: 4px;
    list-style-type: '-';

    li {
      line-height: 24px;
      margin-bottom: 6px;

      .weight {
        float: right;
        padding-top: 2px;
        min-width: 100px;
        text-align: right;

        >span {
          display: inline-block;
          padding: 0 4px;
          line-height: 22px;
          background-color: white;
          border: 1px solid black;
          min-width: 20px;
          text-align: center;
        }
      }
    }
  }
}

.randomAction {
  margin-top: 15px;
  text-align: right;
  padding-right: 4px;
}

.listItem {
  display: flex;
  align-items: center;

  .label {
    display: flex;
    align-items: center;
    width: 100%;
  }

  .input {
    width: 50px;
  }
}

.listItemError {
  color: red;
  display: flex;
  align-items: center;

  .label {
    display: flex;
    align-items: center;
    width: 100%;
  }


  .input {
    width: 50px;
  }
}

.randomItem {
  margin-bottom: 10px;
  padding: 8px;
}

.highlighted {
  border: 2px solid;
  border-radius: 4px;
}