.randomHeader {
  display: flex;
  justify-content: space-between;

  .title {
    margin: 0;
    line-height: 40px;
  }

  .action {
    float: right;
  }


  .settingIcon {
    font-size: 20px;
  }
}



.list {
  padding-left: 7px;
  margin: 0 0 10px;
  list-style-type: '-';

  li {
    line-height: 24px;
    margin-bottom: 6px;

  }
}


.randomAction {
  margin-top: 15px;
  text-align: right;
  padding-right: 4px;
}

.listItem {
  display: flex;
  align-items: center;

  .label {
    display: flex;
    align-items: center;
    width: 100%;
  }
  .input {
    width: 50px;
  }
}

.listItemError {
  color: red;
  display: flex;
  align-items: center;

  .label {
    display: flex;
    align-items: center;
    width: 100%;
  }
}


.randomItem {
  margin-bottom: 10px;
  padding: 8px;
}

.highlighted {
  border: 2px solid;
  border-radius: 4px;
}