
.title {
  margin: 0;
  line-height: 40px;
}

.randomBody {

  .list {
    padding-left: 0px;
    margin: 0 0 10px;
    list-style-type: '-';

    li {
      padding-left: 0;
      line-height: 24px;
      margin-bottom: 6px;

      .weight {
        float: right;
        padding-top: 2px;
        min-width: 100px;
        text-align: right;

        > span {
          display: inline-block;
          padding: 0 4px;
          line-height: 22px;
          background-color: white;
          border: 1px solid black;
          min-width: 20px;
          text-align: center;
        }
      }
    }
  }
}

.label{
  width: 100%;
}

.randomAction {
  margin-top: 15px;
  text-align: right;
  padding-right: 4px;
}

.listItem {
  display: flex;
  align-items: center;

  .input {
    width: 50px;
  }
}

.randomItem {
  margin-top: 10px;
  margin-bottom: 10px;
  padding: 8px;
}
