@import "../../styles/variables.scss";

.surveyGroups {
  max-width: 750px;
  margin: auto;
}

.dropPanel {
  max-height: calc(100vh - 137px);
  overflow-y: auto;

  & > [data-rbd-droppable-id] {
    padding-bottom: 50px;
    padding: 8px;
  }
}

.droppable{
  min-height: calc(100vh - 137px);
}

.footer{
  height: 100px;
}

.headerImage {
  max-height: 22.5vw;
  max-width: 90vw;
  height: 160px;
  width: 640px;
  margin: 12px auto;
  background-size: cover;
  background-position: center;
  border: 1px solid ;
  border-radius: 8px;
}