.answerItem {
  display: flex;
  flex-flow: row wrap;
	align-items: baseline;
  margin-bottom: 15px;

  .answerIcon {
    align-self: center;

    &:hover {
      cursor: pointer;
    }
  }

    .answerIconOther {
      align-self: center;
      margin-right: 10px;
  
      &:hover {
        cursor: pointer;
      }
  }

  .answerNumberOrder {
    width: 30px;
    text-align: center;
  }

  .answerControl {
    margin-left: 7px;
    width: calc(100% - 88px);
  }

  .answerControlRtl {
    margin-right: 7px;
    width: calc(100% - 88px);
  }

  .answerControlOther {
    margin-left: 7px;
    width: calc(100% - 140px);
  }

  .answerControlOtherRtl {
    margin-right: 7px;
    width: calc(100% - 140px);
  }

  .answerOtherControl {
    margin-left: 43px;
    width: 50%;
  }
}