.fullWidth {
  width: 100%;
  cursor: text;
}


.toolbarClass {
  & div .rdw-editor-toolbar {
    border: 0 !important;
    margin: 0 !important;
    padding: 0 !important;
  }
}

.noPadding> :last-child {
  margin-bottom: 0
}

.noPadding> :first-child {
  margin-top: 0
}

P {
  margin-top: 0;
  margin-bottom: 0;
}

.placeholder {
  color: #aaa;

  > :first-child {
    margin-top: 0
  }

  > :last-child {
    margin-bottom: 0
  }

}